import Lib from 'assets/js/Lib'
let page
let location
let searchNo
export default {
  data () {
    return {
      searchNo: ''
    }
  },
  created () {
    page = this
    location = window.location.search
    searchNo = location.slice(10)
  },
  mounted () {
    page.searchNo = searchNo
    const flag = Lib.U.IsPC()
    const bodyC = document.getElementsByTagName('body')[0]
    const loc = window.location.hostname
    if (flag) {
      if (loc === '193.0.1.245') {
        bodyC.className = 'pcBg anXian'
      } else {
        bodyC.className = 'pcBg sanJiang'
      }
    } else {
      if (loc === '193.0.1.245') {
        bodyC.className = 'anXian'
      } else {
        bodyC.className = 'sanJiang'
      }
    }
  }
}




